<template>
  <div>
    <!-- 搜索页面头部 -->
    <div class="search-header">
      <!-- 后退按钮 -->
      <van-icon name="arrow-left" color="white" size="0.48rem" class="goback"
        @click="$router.back()" />
      <!-- 搜索组件 -->
      <van-search v-gfocus placeholder="请输入搜索关键词" background="#007BFF"
        shape="round" @input="inputFn" v-model.trim="kw" @search='searchFn' />
    </div>
    <!-- 搜索建议列表 -->
    <div class="sugg-list" v-if="kw!==''">
      <!-- v-html 将字符串解析成html文档 -->
      <div class="sugg-item" v-for="(item,index) in suggestionList" :key="index"
        v-html="lightFn(item,kw)" @click="suggestClickFn(item)">
        搜索建议
      </div>

    </div>
    <!-- 搜索历史 -->
    <div class="search-history" v-else>
      <!-- 标题 -->
      <van-cell title="搜索历史">
        <!-- 使用 right-icon 插槽来自定义右侧图标 -->
        <template #right-icon>
          <van-icon name="delete" class="search-icon" @click="clearHistoryFn" />
        </template>
      </van-cell>

      <!-- 历史列表 -->
      <div class="history-list">
        <span class="history-item" v-for="(item,index) in history" :key="index"
          @click="historyClickFn(item)">{{item}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { searchSuggestion } from '@/api/index'
import { getStorage, setStorage } from '@/utils/storage'
export default {
  name: 'MySearch',
  data () {
    return {
      kw: '', // 搜索关键字
      timer: null, // 防抖定时器
      suggestionList: [], // 搜索建议列表
      history: JSON.parse(getStorage('history')) || [] // 搜索历史 从本地取出
    }
  },
  watch: {
    history: {
      deep: true,
      handler () {
        // 数组去重
        // ES6新增2个引用类型（Set,Map)
        // set:无序不重复的集合体，如果有重复数据，set会自动清除
        const theSet = new Set(this.history)
        const Arr = Array.from(theSet) // 把Set类型转为Array数组类型
        setStorage('history', JSON.stringify(Arr)) // 搜索历史存入本地
      }
    }
  },
  methods: {

    inputFn (value) {
      clearTimeout(this.timer)
      this.timer = setTimeout(async () => {
        if (this.kw.length === 0) {
          this.suggestionList = []
          return
        } // 如果输入框是空格 无需执行下面代码
        const res = await searchSuggestion(this.kw)
        // console.log(res)
        this.suggestionList = res.data.data.options
      }, 500)
    },
    // 处理字符串高亮
    lightFn (originStr, target) {
      if (originStr === null) return // 如果没有数据 则不处理
      const reg = new RegExp(target, 'ig') // i忽略大小写，g全局匹配
      return originStr.replace(reg, (math) => {
        // math是匹配中时，原字符串里面的字符
        return `<span style="color:red;">${math}</span>`
      })
    },
    searchFn (value) {
      // 输入框回车 跳转到搜索结果界面
      // 注意：路由跳转 在watch之前执行，所以我们要让路由跳转加一个定时器包裹，让跳转在watch之后执行
      if (value.length === 0) return
      this.history.push(value)
      setTimeout(() => {
        this.$router.push(`/search/${value}`)
      })
    },
    suggestClickFn (item) {
      // 点击搜索建议列表 跳转到搜索结果界面
      // 数组添加2种方式
      this.history.push(item)
      // this.history.splice(this.history.length, 1, item)
      setTimeout(() => {
        this.$router.push(`/search/${item}`)
      })
    },
    historyClickFn (item) {
      // 点击搜索历史列表 跳转到搜索结果界面
      this.$router.push(`/search/${item}`)
    },
    // 清除历史数据
    clearHistoryFn () {
      this.history = []
    }
  }
}
</script>

<style scoped lang="less">
.search-header {
  height: 46px;
  display: flex;
  align-items: center;
  background-color: #007bff;
  overflow: hidden;
}
/*后退按钮*/
.goback {
  padding-left: 14px;
}
/*搜索组件*/
.van-search {
  flex: 1;
}

/*搜索建议列表样式 */
.sugg-list {
  .sugg-item {
    padding: 0 15px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    line-height: 50px;
    // 实现省略号的三行代码
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
/**搜索历史 */
.search-icon {
  font-size: 16px;
  line-height: inherit;
}

.history-list {
  padding: 0 10px;
  .history-item {
    display: inline-block;
    font-size: 12px;
    padding: 8px 14px;
    background-color: #efefef;
    margin: 10px 8px 0px 8px;
    border-radius: 10px;
  }
}
</style>
